.test-noopsy {
  min-height: calc(100vh - 60px);
  padding: 8px 30px;
  background: #f4f4f4;
  width: 100%;
  box-sizing: border-box;
}
.test-noopsy .teach-main-box {
  /*max-width: 1440px;*/
  width: 100%;
  padding-top: 60px;
}
.test-noopsy .teach-main-box .teach-main-cont {
  padding: 0 30px;
}
.noopsy-head-wrap {
  position: relative;
  margin: 15px auto 0px;
}
.noopsy-head-wrap .curriculum-top {
  width: 100%;
  margin-bottom: 20px;
}
.noopsy-head-wrap .tohome {
  width: 100%;
  height: 36px;
  line-height: 36px;
  opacity: 1;
  border-radius: 20px;
}
.noopsy-head-wrap .tohome .header-common-index {
  cursor: pointer;
  font-weight: 400;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.9);
  height: 17px;
  font-size: 14px;
}
.noopsy-head-wrap .tohome .anticon {
  color: #fff;
  font-size: 15px;
}
.noopsy-head-wrap .tohome .common-active {
  background: #27cda2;
  border-radius: 5px;
  font-weight: bold;
  color: #ffffff;
  font-size: 14px;
  display: inline-block;
  text-align: center;
  line-height: 30px;
}
.noopsy-head-wrap .tohome .ant-btn {
  min-width: 88px;
}
.noopsy-head-wrap .location:hover .capacity-box {
  display: block;
  transition: all 0.5s ease;
}
.noopsy-head-wrap .location:hover .capacity-box1 {
  display: block;
  transition: all 0.5s ease;
}
.noopsy-head-wrap .location a {
  color: #000;
}
.noopsy-head-wrap .capacity-box {
  position: absolute;
  left: 255px;
  z-index: 999;
  width: 138px;
  background: #fff;
  display: none;
  border: 1px solid #27CDA2;
  margin-bottom: 0;
  border-radius: 5px;
  transition: all 0.5s ease;
  padding: 0 1px;
}
.noopsy-head-wrap .capacity-box P {
  display: block;
  border-radius: 5px;
  padding-left: 14px;
  line-height: 45px;
  color: #333;
  font-size: 14px;
  cursor: pointer;
  margin-bottom: 0;
}
.noopsy-head-wrap .capacity-box P:hover {
  background: #f5f5f5;
  color: #27CDA2;
}
.noopsy-head-wrap .capacity-box1 {
  position: absolute;
  left: 175px;
  z-index: 99999;
  width: 138px;
  background: #fff;
  display: none;
  border: 1px solid #27CDA2;
  margin-bottom: 0;
  border-radius: 5px;
  transition: all 0.5s ease;
  padding: 0 1px;
}
.noopsy-head-wrap .capacity-box1 P {
  display: block;
  padding-left: 14px;
  line-height: 45px;
  color: #333;
  font-size: 14px;
  cursor: pointer;
  margin-bottom: 0;
  border-radius: 5px;
}
.noopsy-head-wrap .capacity-box1 P:hover {
  background: #f5f5f5;
  color: #27CDA2;
}
.noopsy-head-wrap .common-box {
  display: inline-block;
  margin-right: 10px;
  margin-top: 8px;
}
.noopsy-head-wrap .common-box .common-active {
  background: #27cda2;
  border-radius: 5px;
  font-weight: bold;
  color: #ffffff;
  font-size: 14px;
  display: inline-block;
  text-align: center;
  line-height: 30px;
}
.noopsy-head-wrap .TabPaneClass .ant-tabs-bar {
  margin: 0;
}
.noopsy-head-wrap .TabPaneClass .ant-tabs-nav .ant-tabs-tab {
  color: #888;
}
.noopsy-head-wrap .TabPaneClass .ant-tabs-nav .ant-tabs-tab-active {
  color: #2A283D;
  opacity: 1;
}
.noopsy-head-wrap .TabPaneClass .ant-tabs-tab-active {
  border-bottom: 4px solid #3E78ED;
}
.course-btn-lh .ant-btn:hover {
  color: #3E78ED;
  border: 1px solid #3E78ED;
}
.course-btn-lh .ant-btn:focus {
  color: #3E78ED;
  border: 1px solid #3E78ED;
}
.course-btn-lh .ant-btn {
  color: #3E78ED;
  border: 1px solid #3E78ED;
}
.course-tabs {
  display: flex;
  padding: 20px 0;
}
.course-tabs span {
  font-size: 30px;
  font-weight: 600;
  color: #2A283D;
  margin-right: 30px;
  line-height: 140%;
  margin-left: 10px;
}
.course-tabs .ant-tabs {
  height: 40px;
}
.course-tabs .ant-tabs-nav .ant-tabs-tab {
  padding: 0;
  font-size: 14px;
}
.course-tabs .ant-tabs-nav .ant-tabs-tab-active {
  height: 36px;
}
.dialog {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  overflow: auto;
  outline: 0;
  background: rgba(0, 0, 0, 0.8);
}
.dialog .dialog-content {
  text-align: center;
  padding-top: 30px;
  border-radius: 10px;
  overflow: hidden;
  width: 416px;
  height: 496px;
  position: absolute;
  z-index: 2000;
  left: 50%;
  top: 50%;
  background: url('../../../../assets/images/activity/qrcode-bg.jpg');
  background-repeat: no-repeat;
  transform: translate(-50%, -50%);
}
.dialog .dialog-content > p {
  width: 100%;
  display: flex;
  flex-direction: column;
  font-size: 24px;
  color: #FFFFFF;
}
.dialog .dialog-content > p span {
  margin: 4px 0px;
}
.dialog .dialog-content canvas {
  width: 260px !important;
  height: 262px !important;
  margin-bottom: 10px;
}
.dialog .dialog-content .remove {
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  border-radius: 50%;
  position: absolute;
  right: 15px;
  top: 15px;
  font-size: 30px;
  color: #FFFFFF;
  cursor: pointer;
}
.modal-btn-add .ant-modal-content {
  height: auto;
}
.modal-btn-add .ant-modal-content .ant-modal-footer {
  padding-top: 10px!important;
  height: auto!important;
  justify-content: flex-end !important;
}
.dt-style-l {
  padding-left: 25px;
}
.dt-style-l p {
  font-size: 14px !important;
}
